.container {
  margin-left: auto;
  margin-right: auto;
  width: 320px;
  padding-left: 20px;
  padding-right: 20px;
  outline: 1px solid red;
  // width: 100vw;
  @media screen and (min-width: 768px) {
    width: 768px;
    padding-left: 76px;
    padding-right: 76px;
  }
  @media screen and (min-width: 1024px) {
    width: 1024px;
  }
}
